<template>
  <div class="support">
    <el-row>
      <el-col :xs="24"
              :sm="24"
              :md="18"
              :lg="18"
              :xl="18">
        <div class="content">
          <h1>Support</h1>
          <h2>Having trouble or got a specific question for us? We're here to help.</h2>
          <article v-for="(item,index) in contentList"
                   :key="index +'i1'"
                   style="margin:0.5rem 0;">
            <h2>
              <a style="font-weight:600;color:#000"
                 :id="item.title">{{item.title}}</a>
            </h2>
            <div v-for="(item2,index2) in item.list"
                 :key="index2+'i1'">
              <p style="color:#888;font-size:18px;line-height:1.5">
                {{item2.child}}
              </p>
              <ul v-if="item2.list"
                  v-for="(item,index) in item2.list"
                  :key="index">
                <li style="list-style-type: square;">
                  {{item}}
                </li>
              </ul>
              <p v-if="item2.child2"
                 style="color:#888">
                {{item2.child2}}
              </p>
            </div>
          </article>
        </div>
      </el-col>
      <el-col :xs="0"
              :sm="0"
              :md="6"
              :lg="6"
              :xl="6"
              class="daohang hidden-xs-only hidden-sm-only">

        <div v-for="(item,index) in daohaoList"
             :key="index"
             class="list-father">
          <h1 class="list-title"> {{item.title}}</h1>
          <a v-for="(item2,index2) in item.list"
             :key="index2+'i'"
             class="list-a"
             href=""
             @click.prevent="custormAnchor(item2.child)">
            {{item2.child}}
            <br />
          </a>
        </div>
      </el-col>
    </el-row>

  </div>
</template>
<script>
export default {
  data () {
    return {
      contentList: [],
      daohaoList: []
    }
  },
  created () {
    this.contentList = this.i18n.contentList
    this.daohaoList = this.i18n.daohaoList
  },
  computed: {
    i18n () {
      return this.$t('support')
    }
  },
  methods: {
    custormAnchor (anchorName) {
      console.log(anchorName)
      // 找到锚点
      let anchorElement = document.getElementById(anchorName)
      // 如果对应id的锚点存在，就跳转到锚点
      if (anchorElement) { anchorElement.scrollIntoView() }
    }
  }
}
</script>
<style lang="less" scoped>
.support {
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  .content {
    font-size: 20px;
    padding: 1rem;
    line-height: 3;
  }
  .daohang {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    font-size: 22px;
    .list-father {
      padding: 0.5rem;
      background-color: #f1f3f5;
      margin: 0.5rem 0.1rem;
      line-height: 2;
      .list-title {
        // background-color: yellowgreen;
      }
      .list-a {
        // background-color: green;
        padding: 0.1rem;
        // margin: 0.1rem 0;
        // line-height: 1;
        color: #868e96;
        cursor: pointer;
        &:hover {
          background-color: #e7f5ff;
          color: #228be6;
        }
      }
    }
  }
}
</style>
